<template>
  <div>
    <el-table
      v-loading="loading"
      element-loading-text=",,,30"
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.8)"
      :data="tabData"
      style="width: 100%;margin-top: -10px"
      border
      size="mini"
      :row-class-name="tableRowClassName"
      width="100%"
    >
      <!--index,key-->
      <template v-for="(col, index) in tabHeader">
        <!--keyhtml,es6-->
        <!---->
        <el-table-column
          v-if="col.prop === 'action'"
          :label="col.label"
          :key="index.id"
          align="center"
          show-overflow-tooltip
          fixed="right"
        >
          <template slot-scope="scope">
            <el-button
              type="text"
              size="mini"
              @click="clickExec(tabData[scope.$index])"
              >
            </el-button>
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
            >
              <el-button
                type="text"
                size="mini"
                @click="detailClick(tabData[scope.$index].id)"
                >
              </el-button>
            </el-tooltip>
            <el-button
              type="text"
              size="mini"
              :disabled="true"
              @click="deleteCase(tabData[scope.$index].id)"
              >
            </el-button>
            <el-button
              type="text"
              size="mini"
              @click="copyCase(tabData[scope.$index].id)"
              :disabled="true"
              >
            </el-button>
          </template>
        </el-table-column>
        <el-table-column
          v-else-if="col.prop === 'step_id'"
          :label="col.label"
          :key="index.id"
          align="center"
          show-overflow-tooltip
          fixed="right"
        >
          <template slot-scope="scope">
            <a :href="initRef(tabData[scope.$index].step_id)" target="_blank">{{
              tabData[scope.$index].step_id
            }}</a>
          </template>
        </el-table-column>
        <el-table-column
          v-else-if="col.prop === 'id'"
          :prop="col.prop"
          :label="col.label"
          :key="index.id"
          align="center"
          show-overflow-tooltip
          fixed="left"
        >
        </el-table-column>
        <el-table-column
          v-else
          :prop="col.prop"
          :label="col.label"
          :key="index"
          align="center"
          show-overflow-tooltip
        >
        </el-table-column>
      </template>
    </el-table>
    <test-case-dialog
      v-if="dialogVisible"
      :visible="dialogVisible"
      :case-id="caseId"
      :type="caseDialogType"
      @listenToChildEvent="callbackFunc"
    ></test-case-dialog>
    <exec-case-dialog
      v-if="execDialogVisible"
      :visible="execDialogVisible"
      :case-info="caseInfo"
      :exec-type="execType"
      @listenToChildEvent="callbackFuncExec"
    ></exec-case-dialog>
  </div>
</template>

<script>
import TestCaseDialog from '../HttpRunnerTemplate/TestCaseDialog.vue';
import ExecCaseDialog from '../HttpRunnerTemplate/ExecCaseDialog.vue';

export default {
  // 
  name: 'TestCaseTable',
  components: { TestCaseDialog, ExecCaseDialog },

  // (tabData:;tabHeader:;tabHeight:,)
  props: {
    tabData: null,
    tabHeader: null
  },
  data() {
    return {
      execType: 1,
      dialogVisible: false,
      execDialogVisible: false,
      caseId: null,
      caseDialogType: 2,
      loading: false,
      caseInfo: {}
    };
  },
  methods: {
    initRef(stepId) {
      let url = String.format(
        '/httpRunnerTest/httpRunner?activeName=stepModule&id={0}',
        stepId
      );
      return url;
    },
    // 
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 === 0) {
        return 'warning-row';
      } else {
        return 'success-row';
      }
    },
    // 
    clickExec(caseObject) {
      this.execDialogVisible = true;
      this.caseInfo = caseObject;
    },

    // 
    detailClick(id) {
      this.caseId = id;
      this.caseDialogType = 2;
      this.dialogVisible = true;
    },
    // // 
    // copyRequest(id) {
    //   this.editCaseId = id;
    //   this.caseDialogType = 3;
    //   this.dialogVisible = true;
    // },
    // deleteRequest(id) {
    //   this.$confirm(', ?', '', {
    //     confirmButtonText: '',
    //     cancelButtonText: '',
    //     type: 'warning'
    //   })
    //     .then(() => {
    //       let caseInfo = { id: id };
    //       let url = String.format(
    //         '{0}/caseInterface/removeCase',
    //         this.GLOBAL.MONITOR_URL
    //       );
    //       // 
    //       let config = {
    //         headers: {
    //           'Content-Type': 'application/json;charset=UTF-8'
    //         }
    //       };
    //       this.axios
    //         .post(url, JSON.stringify(caseInfo), config)
    //         .then(response => {
    //           let responseCode = response.data.code;
    //           if (responseCode === '200') {
    //             this.Message.success('');
    //           } else {
    //             this.Message.error(response.data.msg);
    //           }
    //           // 
    //           this.$emit('getCaseList');
    //           return response;
    //         })
    //         .catch(response => {
    //           // 
    //           this.$emit('getCaseList');
    //           return response;
    //         });
    //     })
    //     .catch(() => {
    //       this.$message({
    //         type: 'info',
    //         message: ''
    //       });
    //     });
    // },
    callbackFunc(fresh) {
      this.dialogVisible = false;
      if (fresh) {
        this.$emit('getCaseList');
      }
    },
    callbackFuncExec() {
      this.execDialogVisible = false;
    }
  }
};
</script>
<style scoped>
.el-table >>> .warning-row {
  background: oldlace;
}

.el-table >>> .success-row {
  background: #f0f9eb;
}
</style>



